<!-- 工单处理 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">工单处理</p>
      </van-col>
    </van-row>

    <van-row class="dt-content">
      <van-row class="dt-table">
        <van-row class="dt-top">
          <van-col class="dt-item-title">工单编号：</van-col>
          <span class="dt-item-content">{{form.SecunityCode}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">提交人：</van-col>
          <span class="dt-item-content">{{form.Creater}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">提交时间：</van-col>
          <span class="dt-item-content">{{form.CreateTime | filteDate}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">当前处理人：</van-col>
          <span class="dt-item-content">{{form.ChargeUserName}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">工单类型：</van-col>
          <span class="dt-item-content">{{allType[form.SecunityType]}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">工单状态：</van-col>
          <span class="dt-item-content">{{form.SecunityStatus | filteOrderTStatus}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">工单内容：</van-col>
          <span class="dt-item-content">{{form.SecunityContent}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单编号：</van-col>
          <span class="dt-item-content">{{form.OrderNo}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单金额：</van-col>
          <span class="dt-item-content">{{form.OrderMoney}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单商品：</van-col>
          <span class="dt-item-content">{{form.OrderGoods}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">物流公司：</van-col>
          <span class="dt-item-content">{{form.LogisticsCompany}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title-pic">图片：</van-col>
        </van-row>
        <van-row gutter="10"
                 class="dt-pic">
          <van-uploader v-model="newImgList"
                        :deletable="false" />
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title"><span style="color:red;">* </span>完成说明：</van-col>
        </van-row>
        <van-row>
          <van-field class="dt-input"
                     v-model="form.CahrgeContent"
                     rows="3"
                     autosize
                     type="textarea"
                     maxlength="50"
                     placeholder="请输入完成说明"
                     show-word-limit
                     style="width:299px;margin-left:30px;" />
        </van-row>
        <van-row>
          <van-row class="dt-top">
            <van-col class="dt-item-title-pic">上传图片：</van-col>
          </van-row>
          <div class="dt-upImg-box"
               style="text-align:left;">
            <van-uploader v-model="upImgList"
                          :before-read="upImgBefore"
                          :after-read="upImgAfter"
                          :before-delete="deleteImg"
                          :max-count="9"
                          multiple
                          :upload-icon="require('../../../assets/img/加照片.png')" />
          </div>
        </van-row>
      </van-row>
      <van-row class="dt-button-box">
        <van-col :span="6"
                 class="dt-button">
          <van-row>
            <van-image width="19.5"
                       height="20.5"
                       :src="require('../../../assets/img/查看预案.png')"
                       class="dt-checkPlanPic"
                       @click="detailsPlan" />
          </van-row>
          <van-row>
            <div class="dt-checkPlanFont"
                 @click="detailsPlan">查看预案</div>
          </van-row>
        </van-col>
        <van-col :span="8"
                 class="dt-button">
          <van-button round
                      style="width:99.5px;
                             height:40.5px;
                             border-color: #5870F8;"
                      @click="cancel">取消</van-button>
        </van-col>
        <van-col :span="8"
                 class="dt-button">
          <van-button round
                      style="width:99.5px;
                             height:40.5px;"
                      color="#5870F8"
                      @click="confirm">确定</van-button>
        </van-col>
      </van-row>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { GetFileList, Upload, DeleteFile } from '@/api/upFile'
import { GetPageContentByConditions, UpdateItem } from '@/api/workOrder'
import { queryTypeInfos } from '@/api/dimension'
import Compressor from 'compressorjs';
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      form: {
        ID: '', // 唯一，可不传
        OrganizationId: '', // 所属景区
        SecunityCode: '', // 工单编号
        SecunityContent: '', // 工单内容
        SecunityType: null, // 工单类型 Int 1.硬件工单，2.突发工单，3.保洁工单，4.咨询工单， 5.投诉工单，6.商城工单，7，客流预警工单
        SecunityStatus: null, // 工单状态 Int 1.待处理，2.已办理,3.已指派
        longitude: '', // 经度
        latitude: '', // 纬度
        Address: '', // 工单地址
        SecunityBussId: '', // 指派工单ID 转派的时候传当前工单ID
        ChargeUserID: '', // 对接人userid
        ChargeUserName: '', // 对接人名称
        ChargeDeptMent: '', // 对接人部门
        ChargeDuty: '', // 对接人职务
        ChargeLinkPhone: '', // 对接人联系电话
        CahrgeContent: '', // 处理内容 工单处理时的反馈信息
        OperatorUserId: '', // 操作人userid
        OperatorUserName: '', // 操作人名称
        OperationTime: '', // 操作时间
        OperatorLinkOhone: '', // 操作人联系电话
        OrderNo: '', // 订单编号
        OrderMoney: '', // 订单金额
        EventTime: '', // 事发时间
        LinkPhone: '', // 手机号
        UserNmae: '', // 用户
        Creater: '', // 创建人
        CreateTime: '', // 创建时间
        Modifier: '', // 修改人
        ModifyTime: '', // 修改时间
        Validity: 1, // 是否有效（0.无效，1.有效） 传1
        /**********商城工单类字段************/
        OrderNo: '', // 订单编号
        OrderMoney: '', // 订单金额
        OrderGoods: '', // 订单商品
        LogisticsCompany: '', // 物流公司
        // Expend1: 0,
        // Expend2: null,
        // Expend3: null,
      },
      upImgList: [
        // Uploader 根据文件后缀来判断是否为图片文件
        // 如果图片 URL 中不包含类型信息，可以添加 isImage 标记来声明
      ],

      newImgList: [], // 新增时的图片列表
      allType: {},
      // 处理上传后的图片列表
      pictureList: [],
      isStore: false,
    }
  },
  filters: {
    filteDate (date) {
      console.log('过滤器', date)
      if (date) {
        return date.replace('T', ' ')
      }
    },
    filteOrderTStatus (data) {
      let all = {
        1: '待处理',
        2: '已办理',
        3: '已指派',
      }
      return all[data]
    },
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.upImgList = this.$store.state.upImgList
    this.pictureList = this.$store.state.pictureList
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getTypeInfos()
    // 监听浏览器返回按钮
    // window.addEventListener('popstate', this.goBack, false)
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 工单查询
    getworkOrderList () {
      GetPageContentByConditions({
        ID: this.$route.query.ID,
      }).then((response) => {
        this.form = response.Data.ContentList[0]
        this.form.CahrgeContent = this.$store.state.CahrgeContent
        this.getImgList() // 获取图片
        // 判断是否为商城工单
        if (this.form.SecunityType == 6) {
          this.isStore = true
        } else {
          this.isStore = false
        }
      })
    },
    // 处理图片查询
    getImgList () {
      let info = {}
      if (this.form.SecunityBussId == '0') {
        info = {
          AssocID: this.form.ID, // 业务具体id
          PageIndex: 1,
          PageSize: 20,
        }
      } else {
        info = {
          AssocID: this.form.SecunityBussId, // 业务具体id
          PageIndex: 1,
          PageSize: 20,
        }
      }
      GetFileList(info).then((response) => {
        let imgList = response.Data.FileList
        imgList.forEach((item) => {
          if (item.AssocSubModule == '待处理') {
            this.newImgList.push({ url: item.DownloadPath, isImage: true })
          }
        })
      })
    },
    // 处理工单
    updateData () {
      // 处理时将工单状态改为已办
      this.form.SecunityStatus = 2
      this.form.Modifier = JSON.parse(
        localStorage.getItem('userInfo')
      ).real_name
      this.form.ModifyTime = new Date()
      UpdateItem(this.form).then((response) => {
        if (this.$route.query.modelType == 1) {
          this.$router.push({
            name: 'MOOrderHead',
            query: { active: 1 },
          })
        } else if (this.$route.query.modelType == 2) {
          this.$router.push({
            name: 'OMOrderHead',
          })
        } else {
          this.$router.push({
            name: 'Home',
          })
        }
      })
    },
    // 获取工单类型
    getTypeInfos () {
      let info = {
        SysCode: 'ProjectSecunityService',
        Key: 'SecunityType',
      }
      queryTypeInfos(info).then((response) => {
        let type = eval(response.Data.UValue)
        type.forEach((item) => {
          this.allType[item.ItemValue] = item.Description
        })
        this.getworkOrderList()
      })
    },
    // 图片上前的判断
    upImgBefore (file) {
      if (file.type) {
        if (file.type != 'image/jpeg' && file.type != 'image/png') {
          return false
        }
      } else {
        for (let index in file) {
          if (
            file[index].type != 'image/jpeg' &&
            file[index].type != 'image/png'
          ) {
            return false
          }
        }
      }
      return true
    },
    // 图片上传后
    upImgAfter (file) {
      console.log("图片上传后", file)
      let self = this
      if (file instanceof Array) {
        file.forEach((item) => {
          let fileObj = item.file
          new Compressor(fileObj, {
            quality: 0.5,
            success (result) {
              console.log("压缩成功", result);
              self.requestUpImg(result)
            },
            error (err) {
              console.log(err.message);
            },
          })
        })
      } else {
        let fileObj = file.file
        new Compressor(fileObj, {
          quality: 0.5,
          success (result) {
            console.log("压缩成功", result);
            self.requestUpImg(result)
          },
          error (err) {
            console.log(err.message);
          },
        })
      }
    },
    // 发送图片上传请求
    requestUpImg (data) {
      console.log("发送图片上传请求", data);
      let info = {
        assocID: this.form.ID, // 关联业务中某一个具体项的 ID 主键。字符串。最大 50 字节
        module: this.form.SecunityType,
        submodule: '完结', // 关联的业务模块名称。字符串。最大 50 字节。
        Uploader: this.form.OperatorUserName, // 上传人
        file: data,
      }
      if (this.form.SecunityBussId !== '0') {
        info.assocID = this.form.SecunityBussId // 业务具体id
      }

      info = this.objToFormData(info)
      Upload(info).then((response) => {
        this.pictureList.push({
          DownloadPath: response.Data[0].DownloadPath,
          FileID: response.Data[0].FileID,
        })
      })
    },
    // 将对象转换成form-data格式以便文件上传
    objToFormData (data) {
      //对象转formdata格式
      let formData = new FormData()
      for (let key in data) {
        formData.append(key, data[key])
      }
      return formData
    },
    deleteImg (file, d) {
      let delPicture = this.pictureList.splice(d.index, 1)
      let info = [delPicture[0].FileID]
      DeleteFile(info).then((response) => {
        console.log('图片删除返回', response)
      })
      return true
    },
    // 确定按钮
    confirm () {
      if (this.panduan()) {
        this.$dialog
          .confirm({
            title: '标题',
            message: '是否处理工单？',
          })
          .then(() => {
            this.updateData()
          })
          .catch(() => {
            // on cancel
          })
      } else {
        this.$toast('完成说明、上传图片，不同时为空 ')
      }
    },
    // 取消按钮
    cancel () {
      this.$dialog
        .confirm({
          title: '标题',
          message: '是否放弃处理工单？',
        })
        .then(() => {
          // 退出时删除已上传的图片
          this.pictureList.forEach((item) => {
            let info = [item.FileID]
            DeleteFile(info).then((response) => {
              console.log('图片删除返回', response)
            })
          })
          this.$store.commit('upImgList', [])
          this.$store.commit('pictureList', [])
          this.$store.commit('CahrgeContent', '')
          this.$router.go(-1)
        })
        .catch(() => { })
    },
    // 不同时为空
    panduan () {
      if (this.form.CahrgeContent || this.upImgList.length >= 1) {
        return true
      } else {
        return false
      }
    },
    //跳转到预案详情
    detailsPlan () {
      // 将用户输入的内容缓存下来
      this.$store.commit('upImgList', this.upImgList)
      this.$store.commit('pictureList', this.pictureList)
      this.$store.commit('CahrgeContent', this.form.CahrgeContent)
      this.$router.push({
        name: 'DetailsOfPlanInformation',
        query: {
          secunityType: this.allType[this.form.SecunityType],
          come: 'orderProcessing',
        },
      })
    },
    back () {
      this.$store.commit('active', 0)
      history.back(-1)
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}
.dt-content {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
  overflow-y: auto;
  // background-image: url("../../../assets/img/工单背景.png");
  // background-size: 100% 100%;
  // background-attachment: fixed;
  // background-position: 0 calc(#{$headerHeight} + #{$statusBarHeight});
  .dt-table {
    float: left; // 为了清除父子元素外边距重叠
    width: 94%;
    height: 75vh;
    margin: 0 3%;
    margin-top: px2rem(29);
    overflow-y: auto;
    .dt-item-title {
      width: px2rem(90);
      margin-left: px2rem(30);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      color: #4c4c4c;
      font-weight: 500;
      line-height: px2rem(23);
      text-align: left;
    }
    .dt-item-title-pic {
      margin-left: px2rem(30);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      color: #707070;
      font-weight: 500;
      line-height: px2rem(23);
      text-align: left;
    }
    .dt-item-content {
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      font-weight: 400;
      text-align: left;
      color: #4c4c4c;
      line-height: px2rem(23);
      width: px2rem(200);
      float: right;
      padding-right: px2rem(25);
    }
    .dt-input {
      width: px2rem(300);
    }
    .dt-pic {
      margin-top: px2rem(7.5);
      padding: 0 px2rem(30);
      overflow-y: auto;
      .dt-img {
        //图片
        position: relative;
        width: 33%;
        height: 100%;
        float: left;
        padding-top: 50%; /*padding-bottom 都可以*/
        .van-image {
          //图片
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .dt-button-box {
    margin-bottom: px2rem(4.5);
    .dt-button {
      margin-top: px2rem(15);
    }
    .van-button__content {
      font-size: px2rem(14.5);
    }
    .dt-checkPlanPic {
      padding-left: px2rem(40.5);
    }
    .dt-checkPlanFont {
      font-size: px2rem(11);
      color: #999999;
      font-weight: 400;
      font-family: PingFang-SC-Regular;
      padding-left: px2rem(38.5);
    }
  }
  /deep/.van-button--default {
    color: #5870f8;
  }

  .dt-upImg-box {
    margin-left: px2rem(30);
  }
  /deep/.van-uploader__preview {
    position: relative;
    margin: 0 7px 7px 0;
    cursor: pointer;
  }
  /deep/.van-uploader__preview-image {
    display: block;
    width: 89px;
    height: 89px;
    overflow: hidden;
  }
  /deep/.van-uploader__upload {
    width: 89px;
    height: 89px;
    margin: 0 7px 7px 0;
  }
  .dt-top {
    margin-top: px2rem(7.5);
  }
}
/deep/.dt-pic .van-uploader__upload {
  display: none;
}
/deep/.dt-pic .van-uploader {
  float: left;
}
